function formatText(text) {
  return text.replace(/</g, "&lt;").replace(/>/g, "&gt;");
}

// function formatIcon(text) {
//   return text.replace(/</g, "&lt;").replace(/>/g, "&gt;");
// }

export const iconInsert = (_, type) => {
  return `&lt;img src="/static/${type}.png" class="icon" alt="icon" width="40px" vspace="10" hspace="10" 
  align="left" style="margin-right:10px"/&gt;`;
};

export const center = text => {
  return `&lt;div style="margin: 0 auto;
  text-align: center;"&gt;${text}&lt;/div&gt;`;
};

export const pTag = text => {
  return `&lt;p&gt;${formatText(text)}&lt;/p&gt;`;
};
export const ulTag = text => {
  return `&lt;ul&gt;&lt;ul&gt;${formatText(text)}&lt;/ul&gt;&lt;/ul&gt;`;
};
export const olTag = text => {
  return `&lt;ol&gt;&lt;ol&gt;${formatText(text)}&lt;/ol&gt;&lt;/ol&gt;`;
};
export const liTag = text => {
  return `&lt;li&gt;${formatText(text)}&lt;/li&gt;`;
};

export const bTag = text => {
  return `&lt;b&gt;${formatText(text)}&lt;/b&gt;`;
};
export const preTag = text => {
  return `&lt;pre&gt;${formatText(text)}&lt;/pre&gt;`;
};

export const brTag = () => {
  return `&lt;br/&gt;`;
};

export const iTag = text => {
  return `&lt;i&gt;${formatText(text)}&lt;/i&gt;`;
};

export const supTag = text => {
  return `&lt;sup&gt;${formatText(text)}&lt;/sup&gt;`;
};

export const subTag = text => {
  return `&lt;sub&gt;${formatText(text)}&lt;/sub&gt;`;
};

export const greenSpan = text => {
  return `&lt;b&gt;&lt;span style="color:#00b43f ;"&gt;${formatText(
    text
  )}&lt;/span&gt;&lt;/b&gt;`;
};

export const video = text => {
  return `&lt;div style="padding: 10px;border: 2px solid #00b43f;border-radius: 10px;display: flex;width: 640px;
height: 360px;margin: auto;"&gt;
  &lt;iframe src="https://player.vimeo.com/video/${text}" width="640" height="360"
  allow="autoplay; fullscreen; picture-in-picture" allowfullscreen>&lt;/iframe&gt;&lt;/div&gt; &lt;p&gt;&lt;/p&gt;`;
};

export const image = (text, size) => {
  return `&lt;div style="align="center";margin:auto;"&gt;&lt;img src="${text}" width="${size}px"; alt="img"/&gt;
  &lt;/div&gt; &lt;p&gt;&lt;/p&gt;`;
};

export const caption = text => {
  return `&lt;div  style="text-align: center;"&gt;&lt;figcaption style="color:#999999;margin:auto;"&gt;Источник: 
  &lt;a href="${text}" target="_blank" style="color:#999999;text-decoration: underline;"&gt;ТЕКСТ&lt;/a&gt; 
  &lt;/figcaption&gt; &lt;/div&gt; &lt;p&gt;&lt;/p&gt;`;
};

export const line = text => {
  return `&lt;h2&gt;&lt;b&gt;${text}&lt;/b>&lt;/h2&gt;&lt;div style="height: 0;border-bottom: 2px #00b43f solid;
  width: 50%;"/&gt;&lt;p&gt;&lt;/p&gt;`;
};

export const lineLeft = text => {
  return `&lt;div style="border-left:5px solid #00b43f;padding-left: 10px;"&gt;&lt;h2>&lt;b&gt;${text}&lt;/b&gt;
  &lt;/h2&gt;&lt;/div&gt;&lt;p&gt;&lt;/p&gt;`;
};

export const bgGreen = text => {
  return `&lt;div style="background-color: #ebfff0;padding: 15px;"&gt;${text}&lt;/div&gt;&lt;p&gt;&lt;/p&gt;`;
};
export const bgRed = text => {
  return `&lt;div style="background-color: #ffdcd4;padding: 15px;"&gt;${text}&lt;/div&gt;&lt;p&gt;&lt;/p&gt;`;
};

export const bgGrey = text => {
  return `&lt;div style="background-color: #f5f5f5;padding: 15px;"&gt;${text}&lt;/div&gt;&lt;p&gt;&lt;/p&gt;`;
};

export const bgGreenLine = text => {
  return `&lt;div style="border-left: 5px solid #00b43f; padding-left: 10px; padding: 15px; background-color: #eeffe8;"&gt;
 ${text}&lt;/div&gt;&lt;p&gt;&lt;/p&gt;`;
};
export const bgGreyLine = text => {
  return `&lt;div style="border-left: 5px solid #757575; padding-left: 10px; padding: 15px; background-color: #f5f5f5;"&gt;
 ${text}&lt;/div&gt;&lt;p&gt;&lt;/p&gt;`;
};

export const aTag = text => {
  return `&lt;a href="${text}" target="_blank" style="text-decoration: underline;"&gt;ТЕКСТ&lt;/a&gt;`;
};

export const code = text => {
  return `&lt;code style="background-color:#f0f8ff; font-family: Courier New, courier;"&gt;${text}&lt;/code&gt;`;
};

export const frame = text => {
  return `&lt;div style="padding: 15px;border:2px solid #00b43f;max-width: 800px;border-radius:10px; margin:auto;
  text-align:left;"&gt;&lt;span style="color:#00b43f;"&gt;&lt;b&gt;${text}&lt;/b&gt;&lt;/span&gt; &mdash; ОПИСАНИЕ&lt;/div&gt;
  &lt;p&gt;&lt;/p&gt;`;
};

export const circle = text => {
  return `&lt;div style="display:flex;align-items: center;"&gt; &lt;div style="display: flex;margin: 15px;
  align-items: center;justify-content: center;background-color: #f5f5f5;min-width: 40px;height: 40px;border-radius: 50%;
  "&gt;№&lt;/div&gt;&lt;span&gt;${text}&lt;/span&gt;&lt;/div&gt;&lt;p&gt;&lt;/p&gt;`;
};
export const greenCircle = text => {
  return `&lt;div style="display:flex;align-items: center;"&gt; &lt;div style="display: flex;margin: 15px;
  align-items: center;justify-content: center;background-color: #33b549; color:white; min-width: 40px;height: 40px;border-radius: 50%;
  "&gt;№&lt;/div&gt;&lt;span&gt;${text}&lt;/span&gt;&lt;/div&gt;&lt;p&gt;&lt;/p&gt;`;
};

export const trGreen = () => {
  return `style="height: fit-content;background-color: #00b43f;color: #ffffff;font-weight: bold;"`;
};

export const shadow = () => {
  return `style="-webkit-filter: drop-shadow(5px 5px 5px #222); filter: drop-shadow(5px 5px 5px #666666);"`;
};


export const buttonGreen = text => {
  return `&lt;form action="${text}" target="_blank"&gt;
  &lt;button type="submit" style="background-color: #ffffff; border: 0.1em solid #00b43f; border-radius: 5px; 
  color: #00b43f; padding: 0.8em; text-align: center; display: inline-block; transition-duration: 0.4s;
  cursor: pointer;"&gt;НАДПИСЬ&lt;/button&gt;&lt;/form&gt;`;
};

export const imageTextLeft = text => {
  return `&lt;div style="display: flex; flex-direction: row; align-items: center; align-content: center"&gt;
  &lt;img src="${text}" alt="img" width="200"/&gt; &lt;span&gt;ТЕКСТ&lt;/span&gt; &lt;/div&gt;&lt;p&gt;&lt;/p&gt;`;
};

export const imageTextRight = text => {
  return `&lt;div style="display: flex; flex-direction: row; align-items: center; align-content: center"&gt;
  &lt;span&gt;ТЕКСТ&lt;/span&gt;&lt;img src="${text}" alt="img" width="200"/&gt;&lt;/div&gt;&lt;p&gt;&lt;/p&gt;`;
};

export const dash = () => {
  return `&mdash;`;
};
export const ampersand = () => {
  return `&amp;`;
};
export const less = () => {
  return `&lt;`;
};
export const more = () => {
  return `&gt;`;
};
export const quotationMarks = text => {
  return `«${text}»`;
};
export const tooltip = (text, number) => {
  return `&lt;span onclick="toggleImage${number}()" style="background-color: #f1f1f1; border: 0.05em solid #00b43f;
  border-radius: 5px;color: #00b43f;font-weight: bold;padding: 0.1em;text-align: center;
  display: inline-block;transition-duration: 0.4s;cursor: pointer;outline: none;"&gt;${text}&lt;/span&gt;
&lt;div class="hidden" id="hidden-container${number}"&gt;&lt;div style="background-color: #f5f5f5;padding: 15px;"&gt;
  Скрытый текст&lt;br/&gt;&lt;/div&gt;&lt;/div&gt;
  &lt;style&gt;&lt;!--.hidden {display: none;}--&gt;&lt;/style&gt;&lt;script&gt;//&lt;!
  [CDATA[function toggleImage${number}(){document.querySelector('#hidden-container${number}').classList.toggle('hidden');
}// ]]&gt;&lt;/script&gt;`;
};

export const tooltip2 = text => {
  return `&lt;span style="border: solid 1px #4B8A08; background-color: #ebfff0; border-radius: 5px; padding: 5px;" 
  class="text" data-title="ТЕКСТ"&gt;${text}&lt;/span&gt;
&lt;style&gt;&lt;!--
.text {display: inline-block;position: relative;}
.text:hover::after {content: attr(data-title);position: absolute;left: 40px; right: 0; top: 10px;z-index: 1;
background: rgba(235, 255, 240, 0.9); color: #000;width:250px; text-align: left;font-family: Arial, sans-serif;
font-size: 13px; padding: 5px 10px;border: 1px solid #ebfff0; border-radius:5px}
--&gt;&lt;/style&gt;`;
};

export const task = text => {
  return `&lt;h3 style="text-rendering: optimizelegibility; padding: 0px;border: 0px;outline: none;font-variant-numeric: inherit;
   font-variant-east-asian: inherit; font-weight: 400; font-stretch: inherit; font-size: 1.35em; vertical-align: baseline;
   font-family: 'Open Sans','Helvetica Neue',Helvetica,Arial,sans-serif; color: #4c4c4c;"&gt;${text} &lt;/h3&gt;&lt;p&gt;&lt;/p&gt;`;
};

export const details = () => {
  return `&lt;p&gt;&lt;details&gt;&lt;summary&gt;&lt;span style="color: #00b43f;"&gt;Посмотреть ответ&lt;/span&gt;
&lt;/summary&gt; Ответ&lt;/details>&lt;/p&gt;`;
};

export const formula = text => {
  return `&#92;(${text}&#92;)`;
};
export const formulaCenter = text => {
  return `&#92;[${text}&#92;]`;
};
export const ldots = () => {
  return `\\ldots`;
};
export const cdot = () => {
  return `\\cdot`;
};
export const log = () => {
  return `\\log{}`;
};
export const frac = () => {
  return `\\frac{}{}`;
};
export const leqslant = () => {
  return `\\leqslant`;
};
export const geqslant = () => {
  return `\\geqslant`;
};
export const sum = () => {
  return `\\sum_{n}^{i}`;
};
export const uppercase = (text) => {
  return text.toUpperCase();
};

export const quote = text => {
  return `&lt;div style="text-align: left; border-right: 5px solid #33b549; padding-right: 10px;
  text-align: right; color: #00b43f;"&gt; &lt;p style="text-align: right;margin-top: 2em; margin-bottom: 1.5em;color:black;"
  &gt;${text}&lt;/p&gt;&lt;p style="text-align: right;">&lt;em style="color:black;"&gt;Автор&lt;/em&gt;&lt;/p&gt;&lt;/div&gt;`;
};

export const table = () => {
  return `&lt;table&gt;&lt;tr&gt;&lt;td&gt;TEXT&lt;/td&gt;&lt;td&gt;TEXT&lt;/td&gt;&lt;/tr&gt;
&lt;tr&gt;&lt;td&gt;TEXT&lt;/td&gt;&lt;td&gt;TEXT&lt;/td&gt;&lt;/tr&gt;&lt;/table&gt;`;
};

export const videoFull = text => {
  return `&lt;div style="padding:56.25% 0 0 0;position:relative;"&gt;
        &lt;iframe src="https://player.vimeo.com/video/${text}?title=0&byline=0&portrait=0"
        style="position:absolute;top:0;left:0;width:100%;height:100%;"
        frameborder="0" allow="autoplay; fullscreen" allowfullscreen&gt;&lt;/iframe>&lt;/div&gt;
        &lt;script src="https://player.vimeo.com/api/player.js"&gt;&lt;/script&gt;&lt;p&gt;&lt;/p&gt;`;
};

export const summary = (text) => {
  return `&lt;div style="background-color: #ebfff0; padding: 15px; border-radius: 10px;"&gt;
  &lt;img src="/static/checked_2.png" width="40" vspace="10" hspace="10" height="40" align="left" 
  style="margin-right:10px"/&gt;${text}&lt;/div&gt;&lt;p&gt;&lt;/p&gt;`;
};
