              <el-button @click="frac">&#189;</el-button>
              <el-button @click="ldots">...</el-button>
              <el-button @click="leqslant">⩽</el-button>
              <el-button @click="geqslant">⩾</el-button>
              <el-button @click="sum">Sum</el-button>
            </div>
          </transition>
        </div>
      </div>
