<script>
import icons from "@/components/icons";
import {
  ampersand,
  aTag,
  bgGreen,
  bgGreenLine,
  bgGrey,
  brTag,
  bTag,
  buttonGreen,
  caption,
  cdot,
  center,
  circle,
  code,
  dash,
  details,
  formula,
  formulaCenter,
  frac,
  frame,
  geqslant,
  greenSpan,
  image,
  imageTextLeft,
  imageTextRight,
  iTag,
  ldots,
  leqslant,
  less,
  line,
  lineLeft,
  liTag,
  log,
  more,
  olTag,
  preTag,
  pTag,
  quotationMarks,
  subTag,
  supTag,
  tooltip,
  trGreen,
  ulTag,
  video,
  sum,
  tooltip2,
  task,
  greenCircle,
  quote,
  table,
  shadow,
  videoFull,
  bgRed,
  bgGreyLine,
  summary,
  uppercase,
} from "/src/constants";

export default {
  name: "Home",
  components: {
    icons
  },
  computed: {
    getTextarea: {
      get() {
        return this.textarea;
      },
      set(value) {
        this.textarea = value;
      }
    },
    editOrShow() {
      return this.editText ? "Edit" : "Show";
    },
    right: {
      get() {
        return this.rightText;
      },
      set(value) {
        this.rightText = value;
      }
    },
    wrong: {
      get() {
        return this.wrongText;
      },
      set(value) {
        this.wrongText = value;
      }
    },
    getNumber: {
      get() {
        return this.number;
      },
      set(value) {
        this.number = value;
      }
    }
  },
  data() {
    return {
      textarea: ":3",
      selectedText: "",
      latex: false,
      icons: false,
      editText: true,
      cmOptions: {
        // codemirror options
        tabSize: 4,
        mode: "text/javascript",
        theme: "base16-dark",
        lineNumbers: true,
        line: true
      },
      wrongText: "",
      rightText: "",
      showReplace: false,
      number: 0
    };
  },
  methods: {
    selection() {
      this.selectedText = window.getSelection().toString();
      // document.querySelector("#area").addEventListener("mouseup", function () {
      //   this.value.substring(this.selectionStart, this.selectionEnd)
      // });
    },
    exec(action) {
      document.execCommand(
        "insertHTML",
        false,
        action(document.getSelection().toString())
      );
    },
    execTwoElements(action, number) {
      document.execCommand(
        "insertHTML",
        false,
        action(document.getSelection().toString(), number)
      );
    },
    formula() {
      this.exec(formula);
    },
    formulaCenter() {
      this.exec(formulaCenter);
    },
    b() {
      this.exec(bTag);
    },
    i() {
      this.exec(iTag);
    },
    ul() {
      this.exec(ulTag);
    },
    li() {
      this.exec(liTag);
    },
    ol() {
      this.exec(olTag);
    },
    br() {
      this.exec(brTag);
    },
    p() {
      this.exec(pTag);
    },
    pre() {
      this.exec(preTag);
    },
    sup() {
      this.exec(supTag);
    },
    sub() {
      this.exec(subTag);
    },
    details() {
      this.exec(details);
    },
    green() {
      this.exec(greenSpan);
    },
    video() {
      this.exec(video);
    },
    videoFull() {
      this.exec(videoFull);
    },
    image(size) {
      document.execCommand(
        "insertHTML",
        false,
        image(document.getSelection().toString(), size)
      );
    },
    caption() {
      this.exec(caption);
    },
    line() {
      this.exec(line);
    },
    table() {
      this.exec(table);
    },
    lineLeft() {
      this.exec(lineLeft);
    },
    bgGreen() {
      this.exec(bgGreen);
    },
    bgRed() {
      this.exec(bgRed);
    },
    bgGrey() {
      this.exec(bgGrey);
    },
    bgGreenLine() {
      this.exec(bgGreenLine);
    },
    bgGreyLine() {
      this.exec(bgGreyLine);
    },
    a() {
      this.exec(aTag);
    },
    code() {
      this.exec(code);
    },
    frame() {
      this.exec(frame);
    },
    circle() {
      this.exec(circle);
    },
    greenCircle() {
      this.exec(greenCircle);
    },
    trGreen() {
      this.exec(trGreen);
    },
    shadow() {
      this.exec(shadow);
    },
    buttonGreen() {
      this.exec(buttonGreen);
    },
    center() {
      this.exec(center);
    },
    imageTextLeft() {
      this.exec(imageTextLeft);
    },
    imageTextRight() {
      this.exec(imageTextRight);
    },
    dash() {
      this.exec(dash);
    },
    quotationMarks() {
      this.exec(quotationMarks);
    },
    ampersand() {
      this.exec(ampersand);
    },
    less() {
      this.exec(less);
    },
    more() {
      this.exec(more);
    },
    cdot() {
      this.exec(cdot);
    },
    leqslant() {
      this.exec(leqslant);
    },
    geqslant() {
      this.exec(geqslant);
    },
    ldots() {
      this.exec(ldots);
    },
    frac() {
      this.exec(frac);
    },
    log() {
      this.exec(log);
    },
    sum() {
      this.exec(sum);
    },
    replace(text) {
      return text.replace(/&lt;/g, "<").replace(/&gt;/g, ">");
    },
    uppercase() {
      this.exec(uppercase);
    },
    copy() {
      navigator.clipboard.writeText(this.getTextarea);
    },
    replaceAll() {
      this.textarea = this.textarea.replaceAll(this.wrong, this.right);
    },
    remove() {
      this.textarea = "";
    },
    tooltip() {
      console.log(this.getNumber);
      this.execTwoElements(tooltip, this.getNumber);
    },
    tooltip2() {
      this.exec(tooltip2);
    },
    summary() {
      this.exec(summary);
    },
    quote() {
      this.exec(quote);
    },
    task() {
      this.exec(task);
    }
  }
};
</script>

